<template>
  <div>
    <b-modal
      id="modal_receta"
      ok-only
      size="lg"
      class="p-5"
      title="Receta"
    >
      <b-card class="mb-0">
        <b-row>
          <b-col md="12">
            <div class="d-flex">
              <feather-icon
                icon="UserIcon"
                size="19"
              />
              <h5 class="mb-0 ml-50">
                Datos paciente : <span class="text-primary">{{ paciente }} - {{ pacDocumentoIdentidad }}</span>
              </h5>
            </div>
          </b-col>
        </b-row>
      </b-card>
      <b-card class="pr-2 mt-0">
        <b-row>
          <b-col md="12">
            <div class="d-flex">
              <feather-icon
                icon="PlusCircleIcon"
                size="19"
              />
              <h5 class="mb-0 ml-50">
                Adicionar medicamento
              </h5>
            </div>
          </b-col>
        </b-row>
        <b-row class="mt-1">
          <b-col cols="9">
            <label>Medicamento</label>
            <v-select
              v-model="medSeleccionado"
              :filterable="false"
              :dir="$store.state.appConfig.isRTL ? 'rtl' : 'ltr'"
              label="titleMedicamento"
              :options="option"
              placeholder="Buscar medicamento"
              @search="onSearch"
              @input="onSelectSearch"
            >
              <template #selected-option="{title}">
                <div style="display: flex; align-items: baseline">
                  <strong>{{ title }}</strong>

                </div>
              </template>
            </v-select>
          </b-col>
          <b-col cols="2">
            <label>Cantidad</label>
            <b-form-input
              v-model="cantidad"
              type="number"
            />
          </b-col>
          <b-col
            cols="1"
            class="mt-2"
          >
            <b-button
              size="md"
              variant="gradient-success"
              class="mb-1 px-1"
              @click="addMedicamento"
            >
              <feather-icon
                icon="PlusCircleIcon"
                size="19"
              />
            </b-button>
          </b-col>
          <b-col cols="12">
            <label>Indicaciones: </label>
            <b-form-textarea
              v-model="indicaciones"
              rows="2"
              placeholder="Detalle sus indicaciones"
            />
          </b-col>
        </b-row>
      </b-card>
      <b-card class="mt-1">
        <b-row>
          <b-col md="12">
            <div class="d-flex">
              <feather-icon
                icon="ListIcon"
                size="19"
              />
              <h5 class="mb-1 ml-50">
                Lista de medicamentos
              </h5>
            </div>
          </b-col>
        </b-row>
        <b-table
          :items="listaMedicamentos"
          :fields="fields_listaMedicamentos"
          :busy.sync="isBusy"
          size="sm"
          sticky-header="250px"
          small
          head-variant="light"
          :empty-text="'No hay registros para mostrar'"
        >
          <template #cell(medicamento)="row">
            {{ row.item.medicamento.nombre_corto }}
          </template>
          <template #cell(show_indicaciones)="row">
            <span
              class="btn text-center"
            >
              <feather-icon
                :icon="row.detailsShowing ? 'EyeIcon' : 'EyeOffIcon'"
                size="19"
                @click="row.toggleDetails"
              />
            </span>

          </template>

          <template
            #row-details="row"
          >
            <b-card class="m-0 p-0">
              <b-row>
                <b-col
                  sm="3"
                  class="text-sm-right"
                >
                  <b>Indicaciones:</b>
                </b-col>
                <b-col>{{ row.item.indicaciones }}</b-col>
              </b-row>
            </b-card>
          </template>
          <template #cell(actions)="row">
            <b-button
              size="sm"
              variant="outline-warning"
              @click="editarMedicamento(row.item)"
            >
              <feather-icon
                icon="Edit2Icon"
                size="12"
              />
            </b-button>
            <b-button
              size="sm"
              variant="outline-danger"
              @click="quitarMedicamento(row.item.id)"
            >
              <feather-icon
                icon="Trash2Icon"
                size="12"
              />
            </b-button>
          </template>
          <template #cell(index)="row">
            {{ row.index + 1 }}
          </template>
        </b-table>
      </b-card>

      <template #modal-footer>
        <div class="w-100">
          <b-button
            variant="gradient-primary"
            size="sm"
            class="float-right"
            @click="imprimirReceta"
          >
            Grabar e Imprimir <feather-icon
              icon="PrinterIcon"
              size="19"
            />
          </b-button>
        </div>
      </template>
    </b-modal>
    <b-modal
      id="modal_editar_medicam_receta"
      ok-only
      size="sm"
      class="p-5"
      title="Editar medicamento"
      centered
    >
      <b-card>
        <b-form>
          <b-row>
            <b-col cols="12">
              <b-form-group
                label="Medicamento"
                label-for="edit_medicamento"
                label-cols-md="4"
              >
                <b-form-input
                  id="edit_medicamento"
                  v-model="edit_medicam.medicamento_nombre"
                  disabled
                />
              </b-form-group>
            </b-col>
            <b-col>
              <b-form-group
                label="Cantidad"
                label-for="edit_cantidad"
                label-cols-md="4"
              >
                <b-form-input
                  id="edit_cantidad"
                  v-model="edit_medicam.cantidad"
                />
              </b-form-group>
            </b-col>
            <b-col cols="12">
              <b-form-group
                label="Indicaciones"
                label-for="edit_indicaciones"
                label-cols-md="4"
              >
                <b-form-textarea
                  id="adit_indicaciones"
                  v-model="edit_medicam.indicaciones"
                />
              </b-form-group>
            </b-col>
          </b-row>
        </b-form>

      </b-card>
      <template #modal-footer>
        <div class="w-100">
          <b-button
            variant="outline-success"
            size="sm"
            class="float-right"
            @click="updateMedicamentoReceta"
          >
            Actualizar <feather-icon
              icon="RefreshCwIcon"
              size="19"
            />
          </b-button>
        </div>
      </template>
    </b-modal>
  </div>
</template>

<script>
import {
  BButton, BTable, BRow, BCol, BFormInput, BCard, BFormTextarea, BFormGroup, BForm,
} from 'bootstrap-vue'
import store from '@/store'
import router from '@/router'
import { onUnmounted, onMounted } from '@vue/composition-api'
import ToastificationContent from '@core/components/toastification/ToastificationContent.vue'
import atencionRecetaStore from '@/views/salud/atencion/atencion-receta/atencionRecetaStore'
import vSelect from 'vue-select'
import jsPDF from 'jspdf'

export default {
  components: {
    vSelect,
    BButton,
    BRow,
    BCol,
    BTable,
    BFormInput,
    BCard,
    BFormTextarea,
    BFormGroup,
    BForm,
  },
  props: {
    paciente: {
      type: String,
      default: '',
    },
    pacDocumentoIdentidad: {
      type: String,
      default: '',
    },

  },
  data() {
    return {
      atencion_id: router.currentRoute.params.id,

      edit_medicam: {
        indicaciones: '',
        cantidad: '',
        medicamento_nombre: '',
      },

      medSeleccionado: {},
      option: [{
        titleMedicamento: '',
      }],

      newMedicamento: {},
      cantidad: 1,
      indicaciones: '',
      dir: 'ltr',
      selected: [
      ],
      listaMedicamentos: [],
      fields_listaMedicamentos: [
        {
          key: 'index', label: 'N',
        },
        {
          key: 'medicamento', label: 'Medicamento', sortable: true, class: 'text-left',
        },
        { key: 'cantidad', label: 'Cantidad' },
        { key: 'show_indicaciones', label: 'Indicaciones' },
        { key: 'actions', label: 'Acciones' },
      ],
      isBusy: false,
      cadena_a_buscar: '',

      mensajeMinimo: false,
    }
  },
  mounted() {
    this.updateTableMedicamento()
  },
  methods: {
    toastSuccess(text, tipo, titulo) {
      this.$toast({
        component: ToastificationContent,
        props: {
          title: titulo,
          icon: 'BellIcon',
          text,
          variant: tipo,
        },
      })
    },
    addMedicamento() {
      console.log('idAtencion: ', router.currentRoute.params)
      const data = {
        atencion_id: router.currentRoute.params.id,
        medicamento_id: this.medSeleccionado.value,
        cantidad: this.cantidad,
        indicaciones: this.indicaciones,
      }
      store.dispatch('receta-module/addMedicamentoReceta', data).then(res => {
        console.log('res: ', res)
        this.toastSuccess('Medicamento adicionado', 'success', 'xito!')
        this.cantidad = 1
        this.indicaciones = ''
        this.medSeleccionado = []
        this.updateTableMedicamento()
      }).catch(error => {
        console.log(error)
      })
    },
    updateTableMedicamento() {
      this.$http.get(`servicio-salud/atencion/${router.currentRoute.params.id}/receta`).then(res => {
        console.log('resMedicamReceta: ', res)
        this.listaMedicamentos = res.data
      }).catch(error => {
        console.log(error)
      })
    },
    quitarMedicamento(idMedicamentoReceta) {
      store.dispatch('receta-module/removeMedicamentoReceta',
        {
          idAtencion: router.currentRoute.params.id,
          idMedicamentoReceta,
        }).then(res => {
        console.log('resQuitarMedicamento: ', res)
        this.toastSuccess('Medicamento quitado', 'danger', 'xito!')
        this.updateTableMedicamento()
      }).catch(error => {
        console.log(error)
      })
    },
    editarMedicamento(medicamento) {
      console.log('item: ', medicamento)
      this.edit_medicam.atencion_id = this.atencion_id
      this.edit_medicam.medicamento_id = medicamento.id

      this.edit_medicam.medicamento_nombre = medicamento.medicamento.nombre_corto
      this.edit_medicam.indicaciones = medicamento.indicaciones
      this.edit_medicam.cantidad = medicamento.cantidad
      this.$bvModal.show('modal_editar_medicam_receta')
    },
    updateMedicamentoReceta() {
      store.dispatch('receta-module/updateMedicamentoReceta', this.edit_medicam).then(res => {
        console.log('resUpdateMedicamentoReceta: ', res)
        this.toastSuccess('Medicamento actualizado!', 'success', 'xito!')
        this.updateTableMedicamento()
        this.$bvModal.hide('modal_editar_medicam_receta')
        this.edit_medicam.medicamento_nombre = ''
        this.edit_medicam.indicaciones = ''
        this.edit_medicam.cantidad = ''
      }).catch(error => {
        console.log(error)
      })
    },
    imprimirReceta() {
      // eslint-disable-next-line new-cap
      const doc = new jsPDF()

      // doc.addImage('assets/images/fondo_unia.jpeg">', 'JPEG', 100, 10, 160, 180)

      doc.setFontSize(14)
      doc.text('N   -   MEDICAMENTO   -   CANTIDAD', 50, 25)

      doc.text('____________', 50, 30)
      let espacio = 0
      this.listaMedicamentos.forEach((value, index) => {
        doc.setFontSize(12)
        doc.text(`${index + 1}   -   ${value.medicamento}   -   ${value.cantidad}`, 50, 100 + espacio)
        espacio += 8
      })

      doc.save('a4.pdf')
    },
    actualizarCantidad(item) {
      console.log(item)
      // alert('ACtualizamos', item)
    },
    onRowSelected(item) {
      console.log(item)
      // alert('ITEM: ', item)
    },
    onSearch(search, loading) {
      if (search.length >= 3) {
        loading(true)
        const params = { query: search }
        /* const medicamentoPromise = this.$http.get('servicio-salud/farmacia/producto/?medSeleccionado[nombre]=par', data)
        // const medicamentoPromise = this.$http.get(`servicio-salud/farmacia/producto/${medSeleccionado}`)
        Promise.all([medicamentoPromise]).then(response => {
          console.log('response medicamento: ', response)
          this.option = response[0].data.data.map((val, index) => ({
            index, value: val.id, titleMedicamento: `${val.nombre} (${val.stock})`, title: val.nombre,
          }))
          loading(false)
        }).catch(error => {
          console.log(error)
        }) */
        store.dispatch('receta-module/fetchMedicamento', params)
          .then(response => {
            console.log('response medicamento: ', response)
            this.option = response.data.data.map(val => ({
              value: val.id, titleMedicamento: `${val.nombre_completo} (${val.stock})`, title: val.nombre_completo,
            }))
            loading(false)
          }).catch(error => {
            console.log(error)
          })
      } else {
        console.log(search.length)
      }
    },
    onSelectSearch() {
      console.log('elementoSeleccionadoaaaaaaaaaaaaaaaaaaaa: ', this.medSeleccionado)
    },
  },
  setup() {
    const RECETA_STORE_MODULE_NAME = 'receta-module'

    // Register module
    if (!store.hasModule(RECETA_STORE_MODULE_NAME)) store.registerModule(RECETA_STORE_MODULE_NAME, atencionRecetaStore)

    // UnRegister on leave
    onUnmounted(() => {
      if (store.hasModule(RECETA_STORE_MODULE_NAME)) store.unregisterModule(RECETA_STORE_MODULE_NAME)
    })

    onMounted(() => {

    })

    return {

    }
  },
}
</script>

<style lang="scss">
@import '@core/scss/vue/libs/vue-select.scss';
</style>
